<template>
    <div>
        <div id="header">
            <div class="flex-container">
                <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
                <span>我的收藏</span>
            </div>
        </div>
		<div id="recommend" v-if="hascollect">
            <ul>
                <li v-for="item of recommend" :key="item.pid">
                    <router-link :to="'/detail?pid='+item.pid">
                        <img :src="item.pfirst_img">
                    </router-link>
                    <div>
                        <p class="title">我的收藏—{{item.pname}}</p>
                        <p class="price">
                            <span class="price_span">￥{{item.price.toFixed(2)}}</span>
                            <button @click="del_collect(item.pid)">删除</button>
                        </p>
                    </div>
                </li>
            </ul>
		</div>
        <div id="hascollect" v-else>
            <img src="../img/searchnull.gif" alt="kongkong如也">
            <p>什么也没有,快<router-link to="/index">去逛逛</router-link>吧</p>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            recommend:[],
            hascollect:false,
        }
    },
    beforeMount() {
        this.loadMycollect();
    },
    methods: {
        back(){
            this.$router.go(-1);//返回上一层
        },
        loadMycollect(){
            var url="user/get_collect";
            this.axios.get(url).then(result=>{
                this.recommend=result.data;
                if(result.data.length>0){
                    this.hascollect=true;
                }else{
                    this.hascollect=true;
                }
            })
        },
        del_collect(pid){
            var that=this
            var btnArray = ['否', '是'];
            mui.confirm('确定删除?',"delete", btnArray, function(e){
                if(e.index == 1) {
                    var url="user/del_collect?tid="+pid;
                    that.axios.get(url).then(result=>{
                        if(result.data.code==1){
                            that.$toast("删除成功")
                            that.loadMycollect();
                        }
                    })
                }else{
                    return;
                }
            })
        }
    },
}
</script>
<style scoped>
    #header{
        width: 100%;
        height: 45px;
        background:#f99b34;
    }
    #header .flex-container{
        width: 100%;
        height: 100%;
    }
    #header .flex-container a{
        color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
        margin-left: 20px;
        margin-top: 8px;
    }
    #header .flex-container a span{
        display: inline-block;
        font-size: 26px;
        margin-top: 1px;
        margin-left: 1px;
    }
    #header .flex-container>span{
        display: inline-block;
        position: absolute;
        font-size: 18px;
        right: 50%;
        top: 13px;
        margin-right: -18px;
        color: #fff;
    }
    #recommend{
        background: #fff;
        border-top: 1px solid #c8c7cc;
        border-bottom: 1px solid #c8c7cc;
        width: 100%;
        box-shadow: 0px 1px 2px #c8c7cc;
    }
    #recommend>ul{
        display:flex;
        justify-content: space-around;
        flex-wrap: wrap;
        list-style-type: none;
        padding: 0px;
    }
    #recommend>ul>li{
        width: 95%;
        box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
        margin-top: 10px;
        border-radius: 2px;
        overflow: hidden;
    }
    /* #recommend>ul>li a{display: inline-block;} */
    #recommend>ul>li img{
        width: 100%;
        height: auto;
    }
    #recommend>ul>li div .title{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #000;
        font-size: 16px;
    }
    #recommend>ul>li div .price{
        margin-top: 10px;
        margin-bottom: 10px;
        display: flex;
        justify-content: space-between;
        line-height: 30px;
    }
    #recommend>ul>li div button{
        width:100px;
        margin-right: 10px;
    }
    #recommend>ul>li div .price .price_span{
        font-size: 25px;
        color: red;
    }
    #hascollect{
        position: absolute;
        top: 0px;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    #hascollect img{
        margin-top: 200px;
        width: 200px;
        height: 200px;
    }
    #hascollect p{
        font-size: 25px;
    }
    #modal .mui-content>p{
        margin-bottom: 0px;
        line-height: 30px;
    }
</style>


