<template>
    <div>
        <div id="header">
            <div class="flex-container">
                <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
                <span>关于</span>
            </div>
        </div>
        <div id="msg">
            <img src="../img/logo.jpg" alt="">
            <p class="version">Version 1.1.1</p>
        </div>
        <div class="mui-content">
            <ul class="mui-table-view" style="margin-top: 15px;">
                <li class="mui-table-view-cell">
                    <a @click="get_version" class="mui-navigate-right">
                        检查新版本
                    </a>
                </li>
            </ul>
            <ul class="mui-table-view" style="margin-top: 15px;">
                <li class="mui-table-view-cell">
                    <router-link to="/introduction" id="about" class="mui-navigate-right">
                        功能介绍
                    </router-link>
                </li>
            </ul>
            <ul class="mui-table-view" style="margin-top: 15px;">
                <li class="mui-table-view-cell">
                    <router-link to="/announcement" id="about" class="mui-navigate-right">
                        公告
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {

        }
    },
    methods: {
        back(){
            this.$router.go(-1);//返回上一层
        },
        get_version(){
            this.$toast("当前版本已是最新版本");
        }
    },
}
</script>
<style scoped>
    #header{
        width: 100%;
        height: 45px;
        background:#f99b34;
    }
    #header .flex-container{
        width: 100%;
        height: 100%;
    }
    #header .flex-container a{
        color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
        margin-left: 20px;
        margin-top: 8px;
    }
    #header .flex-container a span{
        display: inline-block;
        font-size: 26px;
        margin-top: 1px;
        margin-left: 1px;
    }
    #header .flex-container>span{
        display: inline-block;
        position: absolute;
        font-size: 18px;
        right: 50%;
        top: 13px;
        margin-right: -18px;
        color: #fff;
    }
    #msg{
        width: 100%;
        text-align: center;
        margin: 30px 0;
    }
    #msg img{
        width: 100px;
        border-radius: 10px;
    }
    #msg p{
        font-size: 14px;
        color: #000;
    }
</style>


