<template>
    <div id="all">
		<header class="mui-bar mui-bar-nav">
		    <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
		    <h1 class="mui-title">大图</h1>
		</header>
        <div v-for="(item,i) of list" :key="i" id="container">
            <img :src="item">
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            list:[],
        }
    },
    mounted() {
        this.get_pimg();
    },
    methods: {
        back(){
            this.$router.go(-1);//返回上一层
        },
        get_pimg(){
            var pid=this.$route.query.pid;
            var url="product/get_photo?pid="+pid;
            this.axios.get(url).then(result=>{
                // console.log(result.data.pimg)
                this.list=eval(result.data[0].pimg);
            })
        }
    },
}
</script>
<style scoped>
    #all{
        padding: 60px 0;
    }
    #container{
        width: 100%;
        /* height: 500px; */
    }
    #container img{
        width: 100%;
        height: auto;
        margin: 10px 0;
    }
</style>


