// 完整引入mint-ui组件库
import MintUI from "mint-ui";
Vue.use(MintUI);
//3:引入axios库 main.js
//3.1:引入axios库
import axios from "axios"
//3.2:配置跨域选项
axios.defaults.withCredentials=true
//3.3:将axios配Vue实例中
Vue.prototype.axios = axios
// 4.创建日期格式的过滤器
// 4.1创建过滤器
// 4.2在组件中使用
Vue.filter("datetimeFilter",function(val){
  // (1)创建新日期对象保存原有日期
  var now=new Date(val);
  // (2)创建三个变量保存年月日
  var y=now.getFullYear();
  var m=now.getMonth()+1;
  var d=now.getDate();
  // (3)创建三个变量保存时分秒
  var h=now.getHours();
  var mi=now.getMinutes();
  var s=now.getSeconds();
  // (4)调整月份日期格式
  m<10&&(m="0"+m);
  d<10&&(d="0"+d);
  // (5)返回拼接字符串
  return `${y}-${m}-${d} ${h}:${mi}:${s}`;
})
Vue.filter("unameFilter",function(val){
  var str = String(val)
  var len = str.length;
  var prev,next;
  prev = str.slice(-len,-8);
  next = str.slice(-4);
  str = prev+"****"+next;
  return str;
})
// // eval()
// Vue.filter("evalFilter",function(val){
//   val=eval(val);
//   return val[0];
// })


// 跳转后置顶
router.beforeEach((to, from, next) => {    
  // chrome
  document.body.scrollTop = 0
  // firefox
  document.documentElement.scrollTop = 0
  // safari
  window.pageYOffset = 0
  next()
})

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')



