<template>
    <div class="container">
        <div id="header">
		    <div class="flex-container">
                <div>
                    <router-link to="/index"><span class="mui-icon mui-icon-home"></span></router-link>
                </div>
                <div>
                    <input type="text" placeholder="搜索:目的地/酒店/景点/航班号" @click="get_focus">
                    <span class="mui-icon mui-icon-search"></span>
                </div>
                <div>
                    <router-link to="/myhome"><span class="mui-icon mui-icon-contact"></span></router-link>
                </div>
            </div>
        </div>
        <div class="swiper-container swiper">
            <div class="swiper-wrapper">
                <div class="swiper-slide" @click="select_all(0)" :class="{cafter:cur==0}">
                    全部
                </div>
                <div class="swiper-slide" @click="select_all(1)" :class="{cafter:cur==1}">
                    国内
                </div>
                <div class="swiper-slide" @click="select_all(2)" :class="{cafter:cur==2}">
                    国外
                </div>
            </div>
        </div>
		<div id="concentration" v-if="hasdata">
            <ul>
                <li v-for="item of list" :key="item.pid">
                    <router-link :to="'/detail?pid='+item.pid">
                        <img :src="item.pfirst_img">
		                <div>
                            <p class="title">{{item.pname}}</p>
                            <p class="price">
                                <span class="price_span">￥{{Number(item.price).toFixed(2)}}</span>
                                <span>{{item.pticket}}人已出游</span>
                            </p>
                        </div>
                    </router-link>
                </li>
                <mt-button size="large" type="primary" @click="loadMore">加载更多</mt-button>
            </ul>
		</div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            list:[],
            pno:1,
            cur:0,
            hasMore:true,
            concentration:[],
            hasdata:false,
        }
    },
    created() {
        this.select_all(0)
    },
    mounted() {
        var swiprer=new Swiper('.swiper',{
            slidesPerView:'auto',
            direction:"horizontal",
            observer:true,
            loop:false,
            spaceBetween:15,
            slidesOffsetBefore:15,
            slidesOffsetAfter:15,
        })
    },
    methods: {
        get_focus(){
            this.$router.push({path:'/search'});
        },
        select_all(id){
            this.cur=id;
            var url="product/classify_all?id="+id;
            this.axios.get(url).then(result=>{
                this.list=result.data.data;
                this.hasdata=true;
            })
        },
        loadMore(){
            console.log(this.cur);
            if(this.hasMore==false){
                this.$toast("没有更多的数据了...");
                return;
            }
            this.pno++;
            var url="product/classify_all";
            url+="?pno="+this.pno+"&id="+this.cur;
            this.axios.get(url).then(result=>{
                var rows=this.list.concat(result.data.data);
                this.list=rows;
                console.log(this.list);
                if(this.pno>=result.data.pageCount){
                    this.hasMore=false;
                }
            })
        }
    },
}
</script>
<style scoped>
    #header{
        position: fixed;
        z-index: 999;
        width: 100%;
        height: 45px;
        line-height: 45px;
        background:#f99b34;
    }
    #header .flex-container{
        width: 100%;
        height: 100%;
        display:flex;
        justify-content:space-around;
        align-items:center;
    }
    #header .flex-container div{
        font-size: 16px;
        color: #fff;
    }
    #header .flex-container div span{font-size: 25px;}
    #header .flex-container div a{
        color: #fff;
    }
    #header .flex-container div span.mui-icon-contact{
        line-height:45px;
        font-size:22px;
    }
    #header div:nth-child(2){
        position:relative;
        width: 60%;
    }
    #header div input{
        margin-bottom:0px;
        color: #76838f;
        border-radius:15px;
        height:30px;
        font-size: 14px;
        padding-left: 25px;
        line-height: 25px;
    }
    ::placeholder{font-size: 12px;}
    #header .flex-container div:nth-child(2) span{
        font-size: 20px;
        color: #76838f;
        position: absolute;
        top: 13px;
        left: 5px;
    }
    .swiper-container{
        padding-top: 45px;

    }
    .swiper-slide{
        margin: 10px 0px;
        text-align: center;
        line-height: 30px;
        width: 50px;
        height: 30px;
        background: #fff;
        border-radius: 15px;
        border: 1px solid #3385ff;
        font-size: 14px;
    }
    .cafter{
        background: #3385ff;
        color: #fff;
    }

    #concentration{
        background: #fff;
        border-top: 1px solid #c8c7cc;
        border-bottom: 1px solid #c8c7cc;
        width: 100%;
        margin-bottom: 15px;
        box-shadow: 0px 1px 2px #c8c7cc;
    }
    #concentration::after{
        content: "";
        display: block;
        clear: both;
    }
    #concentration>ul{
        display:flex;
        justify-content: space-around;
        flex-wrap: wrap;
        list-style-type: none;
        padding: 0px;
        margin:0px;
    }
    #concentration>ul>li a{
        display: inline-block;
        width: 100%;
        height: 100%;
    }
    #concentration>ul>li img{
        width: 100%;
        height: auto;
    }
    #concentration>ul>li div .price{
        margin-top: 10px;
        margin-bottom: 0px;
        display: flex;
        justify-content: space-between;
    }
     #concentration>ul{padding: 0px 10px;}
    #concentration>ul>li{
        width: 98%;
        height: 110px;
        margin-top: 10px;
        overflow: hidden;
        border-bottom: 1px solid #ddd;
        padding-bottom: 10px;
    }
    #concentration>ul>li:last-child{border: 0px;padding-bottom: 0px;}
    #concentration>ul>li>a>img{
        float: left;
        width: 40%;
        height: 100%;
    }
    #concentration>ul>li div{
        width: 60%;
        height: 100%;
        float: right;
        padding: 7px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    #concentration>ul>li div .title{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #000;
        font-size: 16px;
    }
    #concentration>ul>li div .price .price_span{
        font-size: 20px;
        color: red;
    }
    #concentration>ul>li div .price span:last-child{font-size: 12px;}
</style>


