<template>
    <div class="container">
        <div id="header">
            <div class="flex-container">
                <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
                <span>通知</span>
            </div>
        </div>
        <div id="news_null" v-if="!havenews">
            <img src="../img/searchnull.gif" alt="暂无通知">
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            havenews:false,
        }
    },
    methods: {
        back(){
            this.$router.go(-1);//返回上一层
        }
    },
}
</script>
<style scoped>
    /* .container{
        position: absolute;
        background: #fff;
        width: 100%;
        height: 100%;
    } */
    #header{
        width: 100%;
        height: 45px;
        background:#f99b34;
    }
    #header .flex-container{
        width: 100%;
        height: 100%;
    }
    #header .flex-container a{
        color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
        margin-left: 20px;
        margin-top: 8px;
    }
    #header .flex-container a span{
        display: inline-block;
        font-size: 26px;
        margin-top: 1px;
        margin-left: 1px;
    }
    #header .flex-container>span{
        display: inline-block;
        position: absolute;
        font-size: 18px;
        right: 50%;
        top: 13px;
        margin-right: -18px;
        color: #fff;
    }
    #news_null{
        position: absolute;
        top: 0px;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: #fff;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    #news_null img{
        width: 200px;
        height: 200px;
    }
</style>


