<template>
    <div>
        <div id="header">
            <div class="flex-container">
                <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
                <span>我的订单</span>
            </div>
        </div>
        <div class="swiper-container swiper">
            <div class="swiper-wrapper">
                <div class="swiper-slide" @click="loadorder(0)" :class="{cafter:cur==0}">
                    待支付
                </div>
                <div class="swiper-slide" @click="loadorder(1)" :class="{cafter:cur==1}">
                    已支付
                </div>
            </div>
        </div>
		<div id="recommend" v-if="hasorder">
            <ul>
                <li v-for="item of orderList" :key="item.pid">
                    <router-link :to="'/detail?pid='+item.pid">
                        <img :src="item.pfirst_img">
                    </router-link>
                    <div>
                        <p class="title">我的订单—{{item.pname}}</p>
                        <p class="price">
                            <span class="price_span">￥{{item.price.toFixed(2)}}</span>
                            <button @click="change_order(item.pid)" v-if="cur==0" class="nopay">支付</button>
                            <button v-else="cur==1" class="payed" disabled>已支付</button>
                        </p>
                    </div>
                </li>
            </ul>
		</div>
        <div id="hascollect" v-else>
            <img src="../img/searchnull.gif" alt="kongkong如也">
            <p>什么也没有,快<router-link to="/index">去逛逛</router-link>吧</p>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            cur:0,
            orderList:[],
            hasorder:false,
            // isLogin:false,
        }
    },
    beforeMount() {
        this.loadorder(0);
    },
    mounted() {
        var swiprer=new Swiper('.swiper',{
            slidesPerView:'auto',
            direction:"horizontal",
            observer:true,
            loop:false,
            spaceBetween:15,
            slidesOffsetBefore:15,
            slidesOffsetAfter:15,
        })
    },
    methods: {
        back(){
            this.$router.go(-1);//返回上一层
        },
        loadorder(id){
            this.cur=id;
            var url="user/get_order?id="+id;
            this.axios.get(url).then(result=>{
                this.orderList=result.data;
                if(result.data.length>0){
                    this.hasorder=true;
                }else{
                    this.hasorder=false;
                }
            })
        },
        change_order(pid){
            var url="user/change_order?tid="+pid;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    this.$toast(result.data.msg);
                    this.loadorder();
                }
            })
        },
    },
}
</script>
<style scoped>
    #header{
        position: fixed;
        z-index: 2;
        width: 100%;
        height: 45px;
        background:#f99b34;
    }
    #header .flex-container{
        width: 100%;
        height: 100%;
    }
    #header .flex-container a{
        color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
        margin-left: 20px;
        margin-top: 8px;
    }
    #header .flex-container a span{
        display: inline-block;
        font-size: 26px;
        margin-top: 1px;
        margin-left: 1px;
    }
    #header .flex-container>span{
        display: inline-block;
        position: absolute;
        font-size: 18px;
        right: 50%;
        top: 13px;
        margin-right: -18px;
        color: #fff;
    }#recommend{
        /* background: #fff; */
        /* border-top: 1px solid #c8c7cc; */
        /* border-bottom: 1px solid #c8c7cc; */
        width: 100%;
        padding-top: 75px;
        /* box-shadow: 0px 1px 2px #c8c7cc; */
    }
    #recommend>ul{
        display:flex;
        justify-content: space-around;
        flex-wrap: wrap;
        list-style-type: none;
        padding: 0px;
        margin: 0px;
    }
    #recommend>ul>li{
        background: #fff;
        width: 95%;
        box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
        margin-top: 10px;
        border-radius: 2px;
        overflow: hidden;
    }
    /* #recommend>ul>li a{display: inline-block;} */
    #recommend>ul>li img{
        width: 100%;
        height: auto;
    }
    #recommend>ul>li div{
        padding: 10px 10px;
    }
    #recommend>ul>li div .title{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #000;
        font-size: 16px;
    }
    #recommend>ul>li div .price{
        margin-top: 10px;
        margin-bottom: 10px;
        display: flex;
        justify-content: space-between;
        line-height: 30px;
    }
    #recommend>ul>li div button{
        width:100px;
        margin-right: 10px;
    }
    #recommend>ul>li div .price .price_span{
        font-size: 25px;
        color: red;
    }
    #hascollect{
        position: absolute;
        top: 0px;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: #fff;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    #hascollect img{
        margin-top: 200px;
        width: 200px;
        height: 200px;
    }
    #hascollect p{
        font-size: 25px;
    }
    #modal .mui-content>p{
        margin-bottom: 0px;
        line-height: 30px;
    }
    .swiper-container{
        position: fixed;
        width: 100%;
        background: #fff;
        padding-top: 45px;
    }
    .swiper-slide{
        margin: 10px 0px;
        text-align: center;
        line-height: 30px;
        width: 50px;
        height: 30px;
        background: #fff;
        border-radius: 15px;
        border: 1px solid #3385ff;
        font-size: 14px;
    }
    .cafter{
        background: #3385ff;
        color: #fff;
    }
</style>


