<template>
    <div> 
        <div id="header">
		    <div class="flex-container">
                <div>
                    <span class="mui-icon mui-icon-location"></span>郑州
                </div>
                <div>
                    <input type="text" placeholder="搜索:目的地/酒店/景点/航班号" @click="get_focus">
                    <span class="mui-icon mui-icon-search"></span>
                </div>
                <div>
                    <router-link to="/myhome"><span class="mui-icon mui-icon-contact"></span></router-link>
                </div>
            </div>
        </div>
        <div id="banner">
            <mt-swipe>
                <mt-swipe-item v-for="item of banner" :key="item.pid">
                    <router-link :to="'/detail?pid='+item.pid"><img :src="item.pfirst_img"></router-link>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <div class="mui-content">
            <ul class="mui-table-view mui-grid-view mui-grid-9">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4">
                    <router-link to="/classify">
                        <span class="mui-icon-extra mui-icon-extra-class"></span>
                        <div class="mui-media-body">分类</div>
                    </router-link>
                </li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4">
                    <router-link to="/myhome">
                        <span class="mui-icon mui-icon-home"></span>
                        <div class="mui-media-body">个人中心</div>
                    </router-link>
                </li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4">
                    <a href="#">
                        <span class="mui-icon-extra mui-icon-extra-hotel"></span>
                        <div class="mui-media-body">旅社</div>
                    </a>
                </li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4">
                    <a href="#">
                        <span class="mui-icon-extra mui-icon-extra-holiday"></span>
                        <div class="mui-media-body">周边</div>
                    </a>
                </li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4">
                    <a href="#">
                        <span class="mui-icon mui-icon-paperplane"></span>
                        <div class="mui-media-body">出行</div>
                    </a>
                </li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4">
                    <a href="#">
                        <span class="mui-icon-extra mui-icon-extra-gift"></span>
                        <div class="mui-media-body">纪念品</div>
                    </a>
                </li>
            </ul>
        </div>
		<div id="preference">
            <div>
                <p>省内特惠</p>
                <p><a href="#">更多>></a></p>
            </div>
            <ul>
                <li v-for="(item,pid) of preference" :key="item.pid">
                    <router-link :to="'/detail?pid='+item.pid">
                        <!-- <router-link :to="'/phone?pid='+item.pid"> -->
                            <img :src="item.pfirst_img">
                        <!-- </router-link> -->
		                <div>
                            <p class="title">{{item.pname}}</p>
                            <p class="price">
                                <span class="price_span">￥{{item.price.toFixed(2)}}</span>
                                <span>{{item.pticket}}人已出游</span>
                            </p>
                        </div>
                    </router-link>
                </li>
            </ul>
		</div>
		<div id="concentration">
            <div>
                <p>旅游精选</p>
                <p><a href="#">更多>></a></p>
            </div>
            <ul>
                <li v-for="item of concentration" :key="item.pid">
                    <router-link :to="'/detail?pid='+item.pid">
                        <img :src="item.pfirst_img">
		                <div>
                            <p class="title">{{item.pname}}</p>
                            <p class="price">
                                <span class="price_span">￥{{item.price.toFixed(2)}}</span>
                                <span>{{item.pticket}}人已出游</span>
                            </p>
                        </div>
                    </router-link>
                </li>
            </ul>
		</div>
		<div id="recommend">
            <div>
                <p>导游推荐</p>
                <p><a href="#">更多>></a></p>
            </div>
            <ul>
                <li v-for="item of recommend" :key="item.pid">
                    <router-link :to="'/detail?pid='+item.pid">
                        <img :src="item.pfirst_img">
		                <div>
                            <p class="title">全国多地—{{item.pname}}</p>
                            <p class="price">
                                <span class="price_span">￥{{item.price.toFixed(2)}}</span>
                                <span>{{item.pticket}}人已出游</span>
                            </p>
                        </div>
                    </router-link>
                </li>
            </ul>
		</div>
        <div id="footer">
            <div class="footer_box">
                <div>
                    <span class="mui-icon mui-icon-phone"></span>
                    <p>电话预定</p>
                </div>
                <div>
                    <span class="mui-icon mui-icon-download"></span>
                    <p>下载客户端</p>
                </div>
                <div>
                    <router-link to="/myhome">
                        <span class="mui-icon mui-icon-contact"></span>
                        <p>我的</p>
                    </router-link>
                </div>
                
            </div>
            <div class="footer_ver">
                <a href="">网站地图</a>|
                <a href="">网站推广</a>|
                <a href="">电脑版</a>
            </div>
            <p>©2019携程旅行 | <a href="http://www.miibeian.gov.cn/">沪ICP备08023580号</a></p>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            banner:[],
            recommend:[],
            preference:[],
            preference_firstimg:[],
            concentration:[],
        };
    },
    created(){
        this.banner_img();
        this.recommend_img();
        this.preference_img();
        this.concentration_img();
    },
    mounted() {
        //document.addEventListener('scroll', this.handleScroll);
        // this.handleScroll();
    },
    methods:{
        // handleScroll () {
        //    var scrollTop =document.documentElement.scrollTop;
        //    var header=document.getElementById("header");
        //     if(scrollTop>0){
        //         header.style.background="red";
        //     }else if(scrollTop==0){
        //         header.style.background="transparent";
        //     }
        // },
        banner_img(){
            var url="product/banner_list";
            this.axios.get(url).then(result=>{
                this.banner=result.data;
            })
        },
        recommend_img(){
            var url="product/recommend_list";
            this.axios.get(url).then(result=>{
                this.recommend=result.data;
            })
        },
        preference_img(){
            var url="product/preference_list";
            this.axios.get(url).then(result=>{
                this.preference=result.data;
            })
        },
        concentration_img(){
            var url="product/concentration_list";
            this.axios.get(url).then(result=>{
                this.concentration=result.data;
            })
        },
        get_focus(){
            this.$router.push({path:'/search'})
        }
    },
}
</script>
<style scoped>
    #header{
        position: fixed;
        z-index: 999;
        width: 100%;
        height: 45px;
        line-height: 45px;
        background:#f99b34;
    }
    #header .flex-container{
        width: 100%;
        height: 100%;
        display:flex;
        justify-content:space-around;
        align-items:center;
    }
    #header .flex-container div{
        font-size: 16px;
        color: #fff;
    }
    #header .flex-container div span{font-size: 16px;}
    #header .flex-container div a{
        color: #fff;
    }
    #header .flex-container div span.mui-icon-contact{
        line-height:45px;
        font-size:22px;
    }
    #header div:nth-child(2){
        position:relative;
        width: 60%;
    }
    #header div input{
        margin-bottom:0px;
        color: #76838f;
        border-radius:15px;
        height:30px;
        font-size: 14px;
        padding-left: 25px;
        line-height: 25px;
    }
    ::placeholder{font-size: 12px;}
    #header .flex-container div:nth-child(2) span{
        font-size: 20px;
        color: #76838f;
        position: absolute;
        top: 13px;
        left: 5px;
    }
    #banner{
        width: 100%;
        height: 230px;
    }
    #banner .mint-swipe-item img{
        width: 100%;
        height: 100%;
    }
    .mui-content ul.mui-table-view{
        margin-top: 0px;
    }
    .mui-content ul.mui-table-view li.mui-table-view-cell span{
        width: 50px;
        height: 50px;
        padding: 7px;
        font-size: 35px;
        color: #fff;
        border-radius:50%;
    }
    .mui-content ul li:nth-child(1) span{background: #29B6F6;}
    .mui-content ul li:nth-child(2) span{background: #E45656;}
    .mui-content ul li:nth-child(3) span{background: #74EDFF;}
    .mui-content ul li:nth-child(4) span{background: #16C57D;}
    .mui-content ul li:nth-child(5) span{background: #D6674D;}
    .mui-content ul li:nth-child(6) span{background: #FFA72A;}
    #recommend,#preference,#concentration{
        background: #fff;
        border-top: 1px solid #c8c7cc;
        border-bottom: 1px solid #c8c7cc;
        width: 100%;
        margin-bottom: 15px;
        box-shadow: 0px 1px 2px #c8c7cc;
    }
    #recommend::after,#preference::after,#concentration::after{
        content: "";
        display: block;
        clear: both;
    }
    #recommend>div,#preference>div,#concentration>div{
        padding: 10px 0px;
        height: 22px;
    }
    #recommend>div>p:first-child,#preference>div>p:first-child,#concentration>div>p:first-child{
        float: left;
        border-left: 4px solid #ac6fd6;
        line-height: 22px;
        margin-left: 7px;
        font-size: 20px;
        text-indent: 12px;
        color: #000;
        margin-bottom: 0px;
    }
    #recommend>div>p:last-child,#preference>div>p:last-child,#concentration>div>p:last-child{
        margin-bottom: 0px;
        float: right;
        padding-right: 20px;
        line-height: 22px;
        font-size: 14px;
        text-indent: 12px;
    }
    #recommend>div>p:last-child a,#preference>div>p:last-child a,#concentration>div>p:last-child a{
        color: #76838f;
    }
    #recommend>ul,#preference>ul,#concentration>ul{
        display:flex;
        justify-content: space-around;
        flex-wrap: wrap;
        list-style-type: none;
        padding: 0px;
    }
    #recommend>ul>li{
        width: 95%;
        box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
        margin-top: 10px;
        border-radius: 2px;
        overflow: hidden;

    }
    #recommend>ul>li a,#preference>ul>li a,#concentration>ul>li a{
        display: inline-block;
        width: 100%;
        height: 100%;
    }
    #recommend>ul>li img,#preference>ul>li img,#concentration>ul>li img{
        width: 100%;
        height: auto;
    }
    #recommend>ul>li div{
        padding: 10px;
    }
    #recommend>ul>li div .title{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #000;
        font-size: 16px;
    }
    #recommend>ul>li div .price,#preference>ul>li div .price,#concentration>ul>li div .price{
        margin-top: 10px;
        margin-bottom: 0px;
        display: flex;
        justify-content: space-between;
    }
    #recommend>ul>li div .price .price_span{
        font-size: 25px;
        color: red;
    }
    #preference>ul>li{
        width: 47%;
        margin-top: 10px;
        border-radius: 2px;
        overflow: hidden;
        box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
    }
    #preference>ul>li div{padding: 7px;}
    #preference>ul>li div .title{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #000;
        font-size: 16px;
        margin: 10px 0;
    }
    #preference>ul>li div .price .price_span{
        font-size: 18px;
        color: red;
    }
    #preference>ul>li div .price span:last-child{
        font-size: 12px;
    }
    
    #concentration>ul{padding: 0px 10px;}
    #concentration>ul>li{
        width: 98%;
        height: 110px;
        margin-top: 10px;
        overflow: hidden;
        border-bottom: 1px solid #ddd;
        padding-bottom: 10px;
    }
    #concentration>ul>li:last-child{border: 0px;padding-bottom: 0px;}
    #concentration>ul>li>a>img{
        float: left;
        width: 40%;
        height: 100%;
    }
    #concentration>ul>li div{
        width: 60%;
        height: 100%;
        float: right;
        padding: 7px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    #concentration>ul>li div .title{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #000;
        font-size: 16px;
    }
    #concentration>ul>li div .price .price_span{
        font-size: 20px;
        color: red;
    }
    #concentration>ul>li div .price span:last-child{font-size: 12px;}



    #footer .footer_box{
        background: #fff;
        width: 100%;
        height: 50px;
        display: flex;
        justify-content: space-around;
    }
    #footer .footer_box>div{
        padding: 5px 0px;
        height: 100%;
        text-align: center;
    }
    #footer .footer_box>div span{
        color: #76838f;
        font-size: 20px;
    }
    #footer .footer_box>div p{
        color: #000;
        margin-bottom: 0;
        line-height: 20px;
    }
    #footer .footer_ver{
        text-align: center;
        padding: 10px 50px;
        display: flex;
        justify-content: space-around;
        color: #666;
    }
    #footer .footer_ver a{
        color: #666;
        font-size: 14px;
    }
    #footer>p{
        text-align: center;
        font-size: 12px;
        color: #999;
    }
    #footer>p>a{
        font-size: 12px;
        color: #999;
    }
</style>


