            <p v-for="(item,index) of recommend" :key="index"><span>★</span> {{item}} </p>
            <!-- <p><span>★</span>【曼谷升级3晚五星酒店】住宿一点不含糊</p> 保证0自费。
            <p><span>★</span>【双岛畅玩】月光岛和格兰岛自由嗨玩</p>
