            <p><span>★</span> 超棒景点：粉红三象神博物馆、拉差达火车头夜市、东芭乐园、杜拉拉水上市场、神殿寺、唐人街</p> -->
        </div>
        <div id="product_introduct">
            <div class="product_tab" :style="pstyle">
