            </div>
            <div id="anchor-0" class="item">
                <h1>产品特色</h1>
                <div class="item_content" v-if="feature">
                    <h2>【超值礼包，倾情赠送】</h2>
                    <p v-for="(item,index) of featrue_ture" :key="index">{{index+1}} . {{item}}</p>
                    <!-- <p>1.价值55镑：欧洲豪华私人宅邸-丘吉尔庄园；</p>
                    <p>2.价值60镑：巨石阵，探索千万年未解之谜；</p>
                    <p>3.价值30镑：周杰伦婚礼举办地-霍华德城堡；</p>
                    <p>4.价值30镑：维多利亚女王行宫—温莎城堡；</p>
                    <p>5.价值80镑：搭乘英国高铁从爱丁堡到约克，在火车上感受不一样的英国之旅；</p>
                    <p>6.价值35镑：爱丁堡城堡，解读英国历史；</p>
                    <p>7.价值25镑：奥克斯**奥特莱斯购物村，乐享购物；</p>
                    <p>8.价值50镑：当地特色美食：炸鱼薯条FISH & CHIPS +“巨石阵饺子”-Pasty或英式下午茶；</p>
                    <p>9.价格200元：赠送每人每天一瓶矿泉水暖心增值服务，加量不加价：</p> -->
                </div>
                <div class="detail_img">
                    <img :src="item" v-for="(item,index) of detail_img" :key="index">
                </div>
            </div>
            <div id="anchor-1" class="item">
                <h1>联运城市</h1>
                <div class="item_content">
                    <p>以下城市可申请往返联运，价格现询客服：</p>
                    <p>华北：太原、青岛、包头、烟台、威海、济南、呼和浩特、大同、长治、鄂尔多斯、乌海、锡林浩特、赤峰、通辽、乌兰浩特、海拉尔、满洲里</p>
                    <p>华东：上海、南京、杭州、合肥、温州、宁波、常州、阜阳、舟山、连云港、南通、黄山、徐州、义乌、盐城、扬州、无锡、赣州、景德镇</p>
                    <p>华中：南昌、武汉、长沙、宜昌、郑州</p>
                    <p>西南：重庆、成都、昆明、贵阳、绵阳、昆明、贵阳、西昌、遵义</p>
                    <p>东北：大连、长春、哈尔滨、沈阳</p>
                    <p>西北：西安、兰州、西宁、乌鲁木齐、银川</p>
                    <p>华南：广州、海口、南宁、福州、厦门、深圳、桂林、柳州、北海、汕头、海口、三亚</p>
                </div>
            </div>
            <div id="anchor-2" class="item">
                <h1>费用须知</h1>
                <div class="item_content font_black">
                    <p><b>费用包含 : </b></p>
                    <p>1.交通:含税费团队/散客机票往返（团队机票将统一出票，散客机票因实时计价预定后即刻出票）,当地旅游巴士</p>
                    <p>2.住宿:酒店标准2人间。</p>
                    <p>3.用餐:行程中团队标准用餐，（中式餐或自助餐或特色餐，含飞机上用餐，自由活动期间用餐请自理；如因自身原因放弃用餐，则餐费不退）。</p>
                    <p>4.门票:行程中所含的景点首道大门票，以具体行程为准。</p>
                    <p>5.导服:专职中文领队兼导游，竭诚为您服务。</p>
                    <p>6.儿童价标准:年龄0~4周岁（不含），不占床,不含餐其他同成人。</p>
                    <p>7.签证:团队旅游签证200元/人。</p>
                </div>
                <div class="item_content font_black">
                    <p><b>费用不包含 : </b></p>
                    <p>1.单房差:单房差（以团期报价为准）。</p>
                    <p>2.门票:行程中注明需要另行支付的自费景点（具体请参考行程描述）。</p>
                    <p>3.补充:出入境个人物品海关征税，超重行李的托运费、保管费。,因交通延阻、战争、政变、罢工、天气、飞机机器故障、航班取消或更改时间等不可抗力原因所引致的额外费用。,酒店内洗衣、理发、电话、传真、收费电视、饮品、烟酒等个人消费。,当地参加的自费以及以上“费用包含”中不包含的其它项目。</p>
                    <p>4.旅游意外险:旅游人身意外保险。</p>
                    <p>5.导服费:120英镑/人。</p>
                </div>
            </div>
            <div id="anchor-3" class="item">
                <h1>出行须知</h1>
                <div class="item_content font_black">
                    <p>因甲方原因发生拒签、延时交签造成本次出境行程变更或取消的，乙方不承担违约责任，甲方需承担申办签证费用____/人。</p>
                    <p>本团机位订金____元/人，未送签证前，如因甲方原因取消行程的，已收取的机位订金不予退还，同时按照旅游合同相关条款保留收取甲方违约金的权利。</p>
                    <p>因甲方原因送签后或者出签后造成本次行程变更或取消行程的，乙方不承担任何违约责任，甲方需承担申办签证费用____/人和机位订金等实际已经发生的费用。</p>
                    <p>此合同签订后即为出机票的有效确认，旅行社到出票时间点直接出票，恕不另行告知。</p>
                    <p>若甲方因为个人原因无法出入境或提供的护照扫描件与在机场登记护照信息不符，责任自负，乙方不承担责任。</p>
                    <p>特别提醒：此线路团款中不包含甲方所在地到出境城市的往返交通费及住宿费用，如产生此部分费用，请客人自理。建议旅游者在获悉此团队已送签证后再着手预订国内段交通及住宿。</p>
                    <p>联运航班不保证当天航班，去程可能会提前一天，回程可能会晚一天。若产生住宿费由客人自理。最终以实际出票情况为准。</p>
                    <p>预订时请告之您的出游人数、出发日期、住宿、用餐标准、以及您的特殊需求。</p>
                    <p>此团收客人数不足20人时，本公司会于出发前7个工作日通知取消该行程，您可以选择延期出发、更改线路出行，或退回团款。</p>
                    <p>团队游览中不允许擅自离团（自由活动除外），如有不便敬请谅解。</p>
                    <p>销签说明：团队返回后，请务必将护照及返程登机牌的原件交于领队统一办理销签手续（其中登机牌原件递送进领馆后是不予退还的，如您需要换取航空公司的积分，可在机场换取登机牌时同时办理）；且按照各使领馆现行规定，会抽查团队部分或全部出团游客回国后面试销签，届时抽查到的游客必须按照使馆通知的时间本人前往使馆面试销签。</p>
                    <p>如果您已自备签证，请务必提供有效护照及签证复印件，用于核对姓名和签证有效期。如因自备签证问题造成行程受阻，相应损失需自行承担。</p>
                </div>
                <div class="item_content font_black">
                    <h2>特殊限制</h2>
                    <p><b>· 病患者、孕妇及行动不便者</b></p>
                    <p>1.为了确保旅游顺利出行，防止旅途中发生人身意外伤害事故，请旅游者在出行前做一次必要的身体检查，</p>
                    <p>如存在下列情况，因服务能力所限无法接待： </p>
                    <p>（1）传染性疾病患者，如传染性肝炎、活动期肺结核、伤寒等传染病人； </p>
                    <p>（2）心血管疾病患者，如严重高血压、心功能不全、心肌缺氧、心肌梗塞等病人；</p>
                    <p>（3）脑血管疾病患者，如脑栓塞、脑出血、脑肿瘤等病人； </p>
                    <p>（4）呼吸系统疾病患者，如肺气肿、肺心病等病人；</p>
                    <p>（5）精神病患者，如癫痫及各种精神病人； </p>
                    <p>（6）严重贫血病患者，如血红蛋白量水平在50克/升以下的病人；</p>
                    <p>（7）大中型手术的恢复期病患者； </p>
                    <p>（8）孕妇及行动不便者。 </p>
                    <p><b>· 老年人</b></p>
                    <p>1. 70周岁以上老年人预订出游，须与我司签订《健康证明》并有家属或朋友（因服务能力所限无法接待及限制接待的人除外）陪同方可出游。</p>
                    <p>2.因服务能力所限，无法接待80周岁以上的旅游者报名出游，敬请谅解。</p>
                    <p>3.因服务能力所限，不受理65周岁（含65周岁）以上老年人预订云南跟团产品。</p>
                    <p>4.因服务能力所限，不受理70周岁（含70周岁）以上老年人预订四川跟团产品。</p>
                    <p><b>· 未成年人</b></p>
                    <p>1.未满18周岁的旅游者请由家属（因服务能力所限无法接待及限制接待的人除外）陪同参团。</p>
                    <p>2.因服务能力所限，无法接待18周岁以下旅游者单独报名出游，敬请谅解。</p>
                    <p><b>· 外籍友人</b></p>
                    <p>本产品网上报价适用持有大陆居民身份证的游客。如您持有其他国家或地区的护照，请在预订过程中注明。 </p>
                </div>
            </div>
        </div>


        <div id="reserve">
            <span class="mui-icon-extra mui-icon-extra-heart-filled" v-if="islike" id="like" @click="changeLike"></span>
            <span class="mui-icon-extra mui-icon-extra-heart" v-else  @click="changeLike"></span>
            <div class="button" @click="subscribe">
                立即预订
            </div>
        </div>
    </div>

</template>
<script>
export default {
    data() {
        return {
            detail:[],
            banner:[],
            recommend:[],
            feature:false,//产品特色
            featrue_ture:[],
            detail_img:[],
            list:["产品特色","联运城市","费用须知","出行须知"],
            pstyle:{position:"fixed"},
            pastyle:{},
            pbstyle:{position:"fixed",top:"0px","z-index":1000},
            islike:false,
            tid:"",
            uname:"",
        }
    },
    created() {
        this.get_detail()
    },
    mounted() {
        window.addEventListener("scroll", this.handleScroll);
    },
    methods: {
        back(){
            this.$router.go(-1);//返回上一层
        },
        get_detail(){
            this.tid=this.$route.query.pid;
            var url="product/detail?pid="+this.$route.query.pid;
            this.axios.get(url).then(result=>{
                this.detail=result.data[0];
                // console.log(eval(this.detail.pimg))
                this.banner=eval(this.detail.pimg);
                // console.log(this.detail.precommend);
                this.recommend=eval(this.detail.precommend);
                if(this.detail.pfeature==null){
                    this.feature=false;
                }else{
                    this.featrue_ture=eval(this.detail.pfeature);
                    this.feature=true;
                }
                this.detail_img=eval(this.detail.pdetails_img)
            })
        },
        goAnchor(selector) {
            var anchor = this.$el.querySelector(selector);
            document.documentElement.scrollTop = anchor.offsetTop;
        },
        handleScroll(){
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || 	document.body.scrollTop;
            if(scrollTop>600){
                this.pstyle=this.pbstyle;
            }else{
                this.pstyle=this.pastyle;
            }
        },
        changeLike(){
            var url="user/hasUname"
            this.axios.get(url).then(result=>{
                if(result.data.code==-1){
                    this.$toast(result.data.msg);
                    setTimeout(()=>{
                        this.$router.push('/login?uid=0');
                    },2000);
                }else{
                    this.uname=result.data;
                    if(this.islike==false){
                        var url1="user/add_collect?tid="+this.tid+"&uname="+this.uname;
                        this.axios.get(url1).then(result=>{
                            if(result.data.code==1){
                                this.$toast(result.data.msg);
                                this.islike=true;
                            }else if(result.data.code==0){
                                this.$toast(result.data.msg);
                                this.islike=false;
                            }
                        })
                    }else{
                        var url1="user/del_collect?tid="+this.tid;
                        this.axios.get(url1).then(result=>{
                            if(result.data.code==1){
                                this.$toast(result.data.msg);
                                this.islike=false;
                            }
                        })
                    }
                }
            })
        },
        subscribe(){
           var url="user/hasUname"
            this.axios.get(url).then(result=>{
                if(result.data.code==-1){
                    this.$toast(result.data.msg);
                    setTimeout(()=>{
                        this.$router.push('/login?uid=0');
                    },2000);
                }else{
                    this.uname=result.data;
                    var url1="user/add_order?tid="+this.tid+"&uname="+this.uname;
                    this.axios.get(url1).then(result=>{
                        if(result.data.code==1){
                            this.$toast(result.data.msg);
                        }else if(result.data.code==0){
                            this.$toast(result.data.msg);
                        }
                    })
                }
            })
        }
    },
}
</script>
