    }
    #recommend p{
        margin-left:10px;
        color: #666;
        font-size: 12px;
        margin-bottom: 0px;
    }
    #recommend p span{
        color: #f60;
    }
    #product_introduct .product_tab{
        width: 100%;
        height:40px;
        line-height: 40px;
        background: #404040;
        display: flex;
        justify-content: space-around;
    }
    #product_introduct .product_tab a{
        color: #F60;
        font-size: 14px;
    }
    #product_introduct .item{
        background: #fff;
        padding: 40px 15px 20px 15px;
        /* border-top: 1px solid #999; */
    }
    #product_introduct .item  h1{
        border-left: 3px solid #f60;
        height: 16px;
        font-size: 16px;
        padding-left: 15px;
        font-weight: normal;
        margin-bottom: 7px;
    }
    #product_introduct .item .item_content h2{
        font-size: 16px;
        font-weight: normal;
        padding: 10px 0;
    }
    #product_introduct .item .item_content p{
        color: #f60;
        font-size: 13px;
        margin-bottom: 0px;
        line-height: 1.8;
        text-align: justify;
    }
    #product_introduct .item .detail_img img{
        width: 100%;
        height: auto;
    }
    #product_introduct .item .font_black p{
        font-size: 14px;
        line-height: 26px;
        color: #666;
    }
    #product_introduct .item .item_content p b{
        color: #333;
        background: #999;
        margin: 20px 0;
    }




    #reserve {
        position: fixed;
        bottom: 0px;
        width:100%;
        height: 50px;
        line-height: 50px;
        background: #F60;
        text-align: center;
        color: #fff;
        font-weight: bold;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    /* #reserve #collect{ */
        /* line-height: 50px; */
    /* } */
    #reserve span{
        font-size: 35px;
        color: red;
    }
    #reserve .button{
        width: 70%;
        background: red;
        height: 40px;
        line-height: 40px;
        padding: 0 50px;
        border-radius: 3px;
    }
</style>


