<template>
    <div>
        <div id="header">
            <div class="flex-container">
                <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
            </div>
            <div class="input">
                <input type="text" placeholder="搜索:目的地/酒店/景点/航班号" id="searchKey" v-model="searchKey">
                <span class="mui-icon mui-icon-search"></span>
            </div>
            <div class="button">
                <button @click="searchResult">搜索</button>
            </div>
        </div>
		<div id="concentration" v-if="searchNull==1">
            <div>
                <p>已为您查找到以下{{con_length}}条结果:</p>
            </div>
            <ul>
                <li v-for="item of list" :key="item.pid">
                    <router-link :to="'/detail?pid='+item.pid">
                        <img :src="item.pfirst_img">
		                <div>
                            <p class="title">{{item.pname}}</p>
                            <p class="price">
                                <span class="price_span">￥{{Number(item.price).toFixed(2)}}</span>
                                <span>{{item.pticket}}人已出游</span>
                            </p>
                        </div>
                    </router-link>
                </li>
            </ul>
		</div>
        <div id="search_null" v-else-if="searchNull==-1">
            <img src="../img/searchnull.gif" alt="">
        </div>
        <div id="searchkey_null" v-else><!-- searchNull==0 -->
            <img src="../img/searchnokey.gif" alt="">
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            searchKey:"",
            searchNull:0,//0代表没有输入关键字 -1没有查到 1 查到
            concentration:[],
            con_length:0,
        }
    },
    mounted() {
        document.getElementById('searchKey').focus();
    },
    methods:{
        back(){
            this.$router.go(-1);//返回上一层
        },
        searchResult(){
            var searchKey=this.searchKey;
            if(!searchKey){
                this.$toast("请输入关键词");
                this.searchNull=0;
                return;
            }
            var url="product/searchKey?searchKey="+searchKey;
            this.axios.get(url).then(result=>{
                this.list=result.data;
                this.con_length=result.data.length;
                if(result.data.length==0){
                    this.$toast("没有查询到结果哦");
                    this.searchNull=-1;
                }else{
                    this.searchNull=1;
                }
            })
        },
    }
}
</script>
<style scoped>
    #header{
        position: fixed;
        z-index: 999;
        width: 100%;
        height: 50px;
        background:#f99b34;
        display: flex;
        justify-content: space-around;
    }
    #header .flex-container{
        /* width: 20%; */
        height: 100%;
        text-align: center;
    }
    #header .flex-container a{
        margin: 10px 0px;
        color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
        background: #0c0e0f;
        opacity: 0.5;
        border-radius: 50%;
    }
    #header .flex-container a span{
        display: inline-block;
        font-size: 26px;
        margin-top: 1px;
    }
    #header .input{
        width: 60%;
        height: 100%;
        line-height: 50px;
        position: relative;
    }
    #header .input input{
        margin-bottom:0px;
        color: #76838f;
        border-radius:18px;
        height:35px;
        font-size: 14px;
        padding-left: 30px;
    }
    ::placeholder{font-size: 12px;}
    #header .input span{
        font-size: 20px;
        color: #76838f;
        position: absolute;
        top: 15px;
        left: 10px;
    }
    #header button{
        height: 33px;
        margin: 10px 0px;
        border-radius: 18px;
    }
    #concentration{
        padding-top: 50px;
        background: #fff;
        border-top: 1px solid #c8c7cc;
        border-bottom: 1px solid #c8c7cc;
        width: 100%;
        margin-bottom: 15px;
        box-shadow: 0px 1px 2px #c8c7cc;
    }
    #concentration::after{
        content: "";
        display: block;
        clear: both;
    }
    #concentration>div{
        padding: 10px 0px;
        border-bottom:1px solid #999
    }
    #concentration>div p{
        line-height: 20px;
        margin-left: 7px;
        font-size: 20px;
        text-indent: 12px;
        color: #666;
        margin-bottom: 0px;
    }
    #concentration>ul{
        display:flex;
        justify-content: space-around;
        flex-wrap: wrap;
        list-style-type: none;
        padding: 0px 10px;
    }
    #concentration>ul>li{
        width: 98%;
        height: 110px;
        margin-top: 10px;
        overflow: hidden;
        border-bottom: 1px solid #ddd;
        padding-bottom: 10px;
    }
    #concentration>ul>li a{
        display: inline-block;
        width: 100%;
        height: 100%;
    }
    #concentration>ul>li>a>img{
        float: left;
        width: 40%;
        height: 100%;
    }
    #concentration>ul>li div .price{
        margin-top: 10px;
        margin-bottom: 0px;
        display: flex;
        justify-content: space-between;
    }
    #concentration>ul>li:last-child{
        border: 0px;
        padding-bottom: 0px;}
    #concentration>ul>li div{
        width: 60%;
        height: 100%;
        float: right;
        padding: 7px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }
    #concentration>ul>li div .title{
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        color: #000;
        font-size: 16px;
    }
    #concentration>ul>li div .price .price_span{
        font-size: 20px;
        color: red;
    }
    #concentration>ul>li div .price span:last-child{font-size: 12px;}
    #search_null,#searchkey_null{
        position: absolute;
        padding-top: 50px;
        width: 100%;
        height: 100%;
        background: #fff;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    #search_null img,#searchkey_null img{
        width: 200px;
        height: 200px;
        margin-top: -50px;
    }
    
</style>


