<template>
    <div>
        <div class="container">
            <div id="header">
                <div class="flex-container">
                    <router-link to="/index"><span class="mui-icon mui-icon-home"></span></router-link>
                    <router-link to="/myset"><span class="mui-icon mui-icon-gear"></span></router-link>
                </div>
            </div>
            <div id="background">
                <div class="user-logo" @click="getLogo">
                    <img src="../img/user_logo.png">
                </div>
                <div class="operate" v-if="isLogin" @change="getInfo">
                    <p>Hi! 尊敬的{{uname | unameFilter}}</p>
                </div>
                <div class="operate" v-else>
                    <router-link to="/login" class="login">登录</router-link>
                    <router-link to="/reg" class="reg ">注册</router-link>
                </div>
            </div>
            <div class="mui-content">
                <ul class="mui-table-view">
                    <li class="mui-table-view-cell">
                        <router-link to="/news" class="mui-navigate-right" v-if="isLogin">
                            新消息通知
                        </router-link>
                        <router-link to="/login" class="mui-navigate-right" v-else>
                            新消息通知
                        </router-link>
                    </li>
                    <li class="mui-table-view-cell">
                        <router-link to="/mytravel" class="mui-navigate-right" v-if="isLogin">
                            我的订单
                        </router-link>
                        <router-link to="/login" class="mui-navigate-right" v-else>
                            我的订单
                        </router-link>
                    </li>
                    <li class="mui-table-view-cell">
                        <router-link to="/mycollect" class="mui-navigate-right" v-if="isLogin">
                            我的收藏
                        </router-link>
                        <router-link to="/login" class="mui-navigate-right" v-else>
                            我的收藏
                        </router-link>
                    </li>
                </ul>
                <ul class="mui-table-view" style="margin-top: 25px;">
                    <li class="mui-table-view-cell">
                        <router-link to="/help" class="mui-navigate-right" v-if="isLogin">
                            帮助与反馈
                        </router-link>
                        <router-link to="/login" class="mui-navigate-right" v-else>
                            帮助与反馈
                        </router-link>
                    </li>
                </ul>
                <ul class="mui-table-view" style="margin-top: 25px;">
                    <li class="mui-table-view-cell">
                        <router-link to="/about" id="about" class="mui-navigate-right">
                            关于mui
                        </router-link>
                    </li>
                </ul>
                <ul class="mui-table-view"  style="margin-top: 25px;" v-if="isLogin">
                    <li class="mui-table-view-cell" @click="exit_login">
                        <a style="text-align: center;color: #FF3B30;" id='confirmBtn'>
                            退出登录
                        </a>
                    </li>
                </ul> 
		    </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            isLogin:false,
            uname:""
        }
    },
    mounted() {
        this.getInfo();
    },
    updated() {
        if(this.isLogin){
            this.exit_login();
        }
    },
    methods: {
        getInfo(){
            var url="user/getInfo";
            this.axios.get(url).then(result=>{
                if(!result.data){
                    this.isLogin=false;
                    this.uname="";
                }else{
                    this.isLogin=true;
                    this.uname=result.data;
                }
            })
        },
        getLogo(){
            if(this.uname){
                this.$router.push('/myset?uname='+this.uname);
            }else{
                this.$router.push('/login');
            }
        },
        exit_login(){
            var that=this;
            document.getElementById("confirmBtn").addEventListener('tap',function() {
				var btnArray = ['否', '是'];
				mui.confirm('确定退出?',"exit", btnArray, function(e){
					if(e.index == 1) {
                        var url="user/getUname";
                        that.axios.get(url).then(result=>{
                            if(!result.data){
                                that.isLogin=false;
                                that.uname="";
                            }else{
                                that.isLogin=true;
                                that.uname=result.data;
                            }
                        })
					}else{
						return;
					}
				})
			});
        }
    },
}
</script>
<style scoped>
    #header{
        position: fixed;
        z-index: 999;
        width: 100%;
        height: 45px;
        background:transparent;
    }
    #header .flex-container{
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
    }
    #header .flex-container a{
        color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
    }
    #header .flex-container a:first-child{
        opacity: 0.5;
        line-height: 0px;
        border-radius: 50%;
        background: #76838f;
        margin-left: 20px;
        margin-top: 10px;
        float: left;
    }
    #header .flex-container a:last-child{
        float: right;
        margin-top: 10px;
        margin-right: 20px;
    }
    #header .flex-container a span{
        display: inline-block;
        font-size: 26px;
        margin-top: 1px;
        margin-left: 1px;
    }
    #background{
        padding-top: 50px;
        width: 100%;
        height: 250px;
        background: url("../img/myhome_bg.jpg") no-repeat;
        background-size: 100% 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
    }
    #background .user-logo{
        display: flex;
        justify-content: space-around;
        width: 100px;
        height: 100px;
    }
    #background .user-logo img{
        width: 100px;
        height: 100px;
    }
    #background .operate{
        width: 100%;
        height: 40px;
        display: flex;
        justify-content: space-around;
    }
    #background .operate a{
        display: inline-block;
        width: 130px;
        height: 40px;
        background: #ff9a14;
        line-height: 40px;
        text-align: center;
        border-radius: 20px;
        color: #fff;
    }
    #background .operate p{
        font-size: 18px;
        color: #f99b34;
    }
</style>


