<template>
    <div class="container">
        <div id="header">
            <div class="flex-container">
                <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
                <router-link to="/reg">注册</span></router-link>
            </div>
        </div>
        <div id="title">
            <h1>账号登录</h1>
            <div class="login_input">
                <input type="text" name="uname" autocomplete="off" placeholder="请输入手机号" v-model="uname">
                <input type="password" name="upwd" autocomplete="off" placeholder="请输入密码" v-model="upwd">
                <button @click="login">登录</button>
            </div>
            <p>登录即代表您同意我们的<a>服务协议</a>和<a>隐私政策</a></p>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            uname:"",
            upwd:""
        }
    },
    methods: {
        back(){
            this.$router.go(-1);//返回上一层
        },
        login(){
            var u = this.uname;
            var p = this.upwd;
            var ureg = /^1[3-9]\d{9}$/;
            if(!ureg.test(u)){
                this.$toast("用户名格式不正确，请检查");
                return;
            }
            var preg = /^\w{6,16}$/;
            if(!preg.test(p)){
                this.$toast("密码格式不正确");
                return;
            }
            var url="user/login?uname="+u+"&upwd="+p;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    this.$toast("登录成功,请稍等");
                    var uid=this.$route.query.uid;
                    if(uid){
                        this.$router.go(-1);
                    }else{
                        this.$router.push('/myhome');
                        this.$router.go(0);
                    }
                }else if(result.data.code==-1){
                    this.$toast(result.data.msg);
                }
            })
        }
    },
}
</script>
<style scoped>
    .container{
        position: absolute;
        width: 100%;
        height: 100%;
        background: #fff;
    }
    #header{
        width: 100%;
        height: 45px;
    } 
    #header .flex-container{
        width: 100%;
        height: 100%;
    }
    #header .flex-container a{
        display: inline-block;
    }
    #header .flex-container a:first-child{
        color: #000;
        width: 30px;
        height: 30px;
        line-height: 0px;
        margin-left: 15px;
        line-height: 45px;
        float: left;
    }
    #header .flex-container a:last-child{
        float: right;
        margin-right: 20px; 
        line-height: 45px;
    }
    #header .flex-container a span{
        display: inline-block;
        line-height: 45px;
        font-size: 30px;
    }
    #title h1{
        margin-top: 60px;
        text-align: center;
        font-size: 25px;
        color: #333;
        line-height: 25px;
        font-weight: normal;
    }
    #title .login_input{
        padding: 50px 30px;
    }
    #title .login_input input{
        height: 54px;
        line-height: 24px;
        border-radius: 0px;
        margin-bottom: 0px;
        border: 0px;
        border-bottom: 1px solid #BCBCBC;
        padding: 15px 0px;
        color: #333;
        font-size: 20px;
    }
    ::placeholder{
        color: #bbb;
        font-size: 18px;
    }
    #title .login_input button{
        width:100%;
        height:44px;
        margin-top: 50px;
        background: #ff9a14;
        border-radius: 4px;
        font-size: 18px;
        color: #fff;
        border-color: transparent;
    }
    #title p{
        position: absolute;
        left: 50%;
        bottom: 30px;
        margin-left: -120px;
        text-align: center;
        font-size: 12px;
    }
</style>



