<template>
    <div>
        <div id="header">
            <div class="flex-container">
                <a @click="back"><span class="mui-icon mui-icon-back"></span></a>
                <span>意见反馈</span>
            </div>
        </div>
        <div id="textarea">
            <textarea cols="30" rows="10" v-model="content" placeholder="请提供您宝贵的意见,我们将不断完善" maxlength="250"></textarea>
            <input type="text" v-model="phone" placeholder="请输入您的手机号/邮箱">
            <button type="button" class="mui-btn mui-btn-warning mui-btn-block" @click="submit">提交</button>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            content:"",
            phone:"",
        }
    },
    methods: {
        back(){
            this.$router.go(-1);//返回上一层
        },
        submit(){
            var ureg = /^1[3-9]\d{9}$/;
            if(!this.content){
                this.$toast("请输入内容");
                return;
            }
            if(!this.phone){
                this.$toast("请输入手机号码");
                return;
            }
            if(!ureg.test(this.phone)){
                this.$toast("电话号码格式不正确,请检查");
                return;
            }
            var url="user/submit_help?content="+this.content+"&phone="+this.phone;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    this.$toast(result.data.msg);
                    this.content="";
                    this.phone="";
                }
            })
        }
    },
}
</script>
<style scoped>
    #header{
        width: 100%;
        height: 45px;
        background:#f99b34;
    }
    #header .flex-container{
        width: 100%;
        height: 100%;
    }
    #header .flex-container a{
        color: #fff;
        display: inline-block;
        width: 30px;
        height: 30px;
        margin-left: 20px;
        margin-top: 8px;
    }
    #header .flex-container a span{
        display: inline-block;
        font-size: 26px;
        margin-top: 1px;
        margin-left: 1px;
    }
    #header .flex-container>span{
        display: inline-block;
        position: absolute;
        font-size: 18px;
        right: 50%;
        top: 13px;
        margin-right: -18px;
        color: #fff;
    }
    #textarea{
        padding: 15px;
    }
    #textarea textarea{
        padding: 7px;
    }
    ::placeholder{
        font-size: 14px;
        color: #999;
    }
    button{
        padding: 10px 0;
    }
</style>


