import Vue from 'vue'
import Router from 'vue-router'

import index from "./components/index.vue"
import detail from "./components/detail.vue"
import myhome from "./components/myhome.vue"
import myset from "./components/myset.vue"
import login from "./components/login.vue"
import reg from "./components/reg.vue"
import search from "./components/search.vue"
import news from "./components/news.vue"
import mytravel from "./components/mytravel.vue"
import mycollect from "./components/mycollect.vue"
import about from "./components/about.vue"
import introduction from "./components/introduction.vue"
import announcement from "./components/announcement.vue"
import classify from "./components/classify.vue"
import photo from "./components/photo.vue"
import help from "./components/help.vue"

Vue.use(Router)
export default new Router({
  routes: [
    {path:'/',component:index},
    {path:'/index',component:index},
    {path:'/detail',component:detail},
    {path:'/myhome',component:myhome},
    {path:'/myset',component:myset},
    {path:'/login',component:login},
    {path:'/reg',component:reg},
    {path:'/search',component:search},
    {path:'/news',component:news},
    {path:'/mytravel',component:mytravel},
    {path:'/mycollect',component:mycollect},
    {path:'/about',component:about},
    {path:'/introduction',component:introduction},
    {path:'/announcement',component:announcement},
    {path:'/classify',component:classify},
    {path:'/photo',component:photo},
    {path:'/help',component:help},
  ]
})



